












import Event from '../../common/event.js'; 
var page = 0,totalpage=0;
import mTokenCard from "@/components/m-token-card.vue";
export default {
	name:"m-order-tokens",
	props:{},
	components:{
		mTokenCard
	},
	data(){
		return{
			storeid:'',
			tokens:[],
		}
	},
	methods:{
		getTokens(){
			let _this = this;
			uni.showLoading({});
			if(totalpage&&page > totalpage){
				uni.showToast({"title":"已经加载全部", icon:"none"});
				return ;
			}
			this.$apis.postUsableCoupons({
				storeId:_this.storeid,
				start:page,
				length:20
			}).then(res=>{
				let data = res.data;
				if(data.coupons){
					totalpage=data.pages;
					var newsList = data.coupons;
					this.tokens = this.tokens.concat(newsList);
					uni.hideLoading();
					page++;
				}
				// this.tokens=res.data.coupons;
			})
		},
		choseTokenFn(data){
			console.log('优惠间id：'+data);
			Event.noticeFun(Event.UPDATA_TOKEN, {id:data});
			uni.navigateBack({
				delta:1
			})
		}
	},
	// 加载更多
	onReachBottom(){
		this.getTokens();
	},
	//下拉刷新
	onPullDownRefresh : function(){
		// 重置分页及数据
		page = 1;
		this.tokens =[];
		this.getTokens();
	},
	onLoad(option){
		this.storeid=option.storeid;
		page = 1;
		tokens:[];
		this.getTokens();
	}
}
